$light-1: white;
$light-2: rgb(235, 235, 235);
$light-3: rgb(230, 230, 230);
$light-4: rgb(209, 209, 209);

$primary-1: #EF643C;
$primary-2: #e95c4075;
$secondary-1: #007DB4;

$dark-1: black;
$dark-2: rgb(66, 66, 66);
$dark-3: rgb(129, 129, 129);


.selectItem {
  display: block;
  cursor: pointer;
  padding: 4px;
  &:hover {
    background-color: $primary-2;
  }
}
.selectedItem {
  &_delete {
    padding: 0px 4px 0px 4px;
    background-color: $light-4;
    border-radius: 5px;
    margin-left: 9px;
    &:hover {
      background-color: $light-3;
      & svg {
        color: $dark-2;
      }
    }
    & svg {
      color: $dark-3;
    }
  }
}
.selectSearchable {
  margin-bottom: 32px;
  &_Container {
    padding: .5rem;
    background-color: $light-2;
  }
  &_SearchContainer {
    margin-bottom: 6px;
    & input[type="text"] {
      background-color: white;
      border-radius: 0;
      width: 90%;
      border: none;
      border-bottom: 1px solid black;
      outline: none;
      padding: 10px;
      &:focus {
        outline: none;
      }
    }
    & svg {
      position: relative;
      float: right;
      top: -26px;
      left: -18px;
      cursor: pointer;
      color: black;
      z-index: 20;
    }
  }
  &_ItemsContainer {
    height: 200px;
    overflow: hidden;
    overflow-y: scroll;
  }
  &_Header {
    display: flex;
    justify-content: space-between;
    min-height: 32px;
  }
  &_SelectedContainer {
    background-color: $light-1;
    padding: 8px;
    cursor: pointer;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  &_Trigger {
    background-color: $primary-1;
    color: white;
    width: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  &_SelectedItem {
    background-color: $dark-2;
    color: white;
    font-size: .8rem;
    display: inline-block;
    margin: 0 2px 2px 0;
    padding: 4px 8px;
    border-radius: 5px;
    &_Selected {
      margin: 2px 0;
      background-color: $primary-1;
      color: white;
    }
  }
  &_List {
    position: absolute;
    width: 223px;
    z-index: 10;
    box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
    display: block;
    padding: 10px;
    background-color: $light-3;
    color: black;
  }
}
.listCollapsed {
  display: none;
  overflow-y: auto;
}